.logo {
	&__link {
		display: flex;
		height: 24px;
		color: $white;
		font-size: 31px;
		font-weight: 600;
		line-height: 41px;
		text-transform: uppercase;
		align-items: center;

		&:hover {
			text-decoration: none;
		}

		img {
			max-width: 100%;
		}
	}

	@media (--phone) {
		margin-right: 8px;
		font-size: 20px;
		font-weight: 500;
		line-height: 27px;
		color: var(--blue);

		.mobile & {
			&__link {
				font-size: 24px;
				line-height: 26px;
				color: #007aff;
				font-size: 16px;
				font-weight: 300;
				line-height: 21px;
				text-transform: uppercase;
				letter-spacing: 0.6px;
				width: 108px;
				height: 48px;

				img {
					max-width: 100%;
					max-height: 100%;
				}
			}
		}

	}
}
