// main: style.less

.popup {
	position: fixed;
	overflow-x: hidden;
	overflow-y: scroll;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	opacity: 0;
	pointer-events: none;
	background: rgba(0, 0, 0, 0.54);
	transition: opacity 500ms;
	display: none;

	&_iphone,
	&_iphone body {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
	}

	&_ie10,
	&_ie9 {
		display: none;
	}

	&_dopclass {
		position: absolute;
		overflow: visible;
	}

	&_active {
		opacity: 1;
		pointer-events: auto;
		z-index: 1200000;
		display: block;
	}

	&_ie10.popup_active,
	&_ie9.popup_active {
		display: block;
	}

	&__overlay {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}

	&__table {
		pointer-events: none;
		display: table;
		width: 100%;
		height: 100%;
		padding: 40px 30px;
		transition: .2s;
		transform: translate(0, 40px);
	}

	&_active &__table {
		transform: translate(0, 0);
	}

	&__cell {
		height: 100%;
		height: 100%;
		display: table-cell;
		vertical-align: middle;
		text-align: center;
	}

	&__block {
		display: inline-block;
		text-align: left;
		position: relative;
		pointer-events: auto;
	}

	&_dopclass &__block {
		background: #ccc;
		box-shadow: none;

		&:before {
			position: absolute;
			width: 20px;
			height: 20px;
			background: #ccc;
			transform: rotate(45deg);
			left: -10px;
			top: 21px;
			content: '';
		}
	}

	&__ajax {
		display: none;
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(255, 255, 255, 0.8) url(data:image/svg+xml;base64,) no-repeat center center;
		opacity: 0;
		pointer-events: none;
		z-index: 1000;
		transition: opacity 300ms;

		&.show {
			opacity: 1;
		}
	}

	&__close {
		display: none;
		position: absolute;
		top: 8px;
		right: 8px;
		width: 40px;
		height: 40px;
		cursor: pointer;
		z-index: 2;

		&:before,
		&:after {
			position: absolute;
			left: 50%;
			margin: -1px 0 0 -8px;
			top: 50%;
			height: 2px;
			width: 16px;
			background: var(--blue);
			content: '';
			transform-origin: 50% 50%;
		}

		&:before {
			transform: rotate(-45deg);
		}

		&:after {
			transform: rotate(45deg);
		}
	}

	&_active &__close {
		display: block;
	}

	&__change {
		&>div {
			display: block;
		}
	}

	&__body_hidden {
		overflow: hidden;
		-webkit-overflow-scrolling: auto;
		-moz-overflow-scrolling: auto;
		-o-overflow-scrolling: auto;
		-ms-overflow-scrolling: auto;
		overflow-scrolling: auto;
	}

	&__title {
		font-size: 32px;
		font-weight: 500;
		line-height: 43px;
		color: #000000;
		margin-bottom: 16px;
	}

	&__text {
		font-size: 14px;
		line-height: 1.71;
		color: #000000;
		margin-bottom: 24px;
	}

	.form .checkbox {
		margin-bottom: 0;
	}

	&__footer {
		display: flex;
		justify-content: center;
		padding-top: 16px;
	}

	&__block {
		border-radius: 8px;
		background: #ffffff;
		box-shadow: 0px 4px 12px rgba(23, 29, 41, 0.08);
		// width: 840px;
		padding: 24px 39px;
		max-width: 100%;
	}

	&__box {
		display: flex;
		max-width: 100%;
		justify-content: space-between;
		width: 100%;

		.popup__row {
			flex-grow: 0;
			width: 96px;

			&:last-child {

				width: 152px;
			}
		}
	}

	&__content {
		width: 100%;
		display: flex;

		&.free-call {
			max-width: 420px;
		}
	}

	&__left,
	&__right {
		display: flex;
		// flex-direction: column;
		flex-wrap: wrap;
		align-content: baseline;
		max-width: 100%;
		flex-shrink: 0;

		img {
			max-width: 100%;
			max-height: 100%;
		}
	}

	&__right {
		width: 400px;
	}

	&__left {
		width: 343px;
		margin-right: 20px;
	}

	&__form {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}

	&__subtitle {
		font-size: 12px;
		font-weight: 400;
		line-height: 16px;
		margin-bottom: 12px;
		width: 100%;
	}

	&__input {
		border-radius: 4px;
		border: solid 1px #c8c9cc;
		background: #ffffff;
		padding: 8px 12px;
		flex-grow: 1;
		max-width: 100%;
	}

	input {
		font-size: 16px;
		font-weight: 400;
		line-height: 20px;

		&[type="color"] {
			-webkit-appearance: none;
			border: none;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			margin-right: 8px;
		}

		&[type="color"]::-webkit-color-swatch-wrapper {
			padding: 0;
			border-radius: 50%;
		}

		&[type="color"]::-webkit-color-swatch {
			border: none;
			border-radius: 50%;
		}

		&[type="time"] {
			position: relative;

			&:after {
				content: '';
				background: rgb(255, 255, 255);
				width: 20px;
				height: 20px;
				position: absolute;
				right: 0;
			}
		}
	}

	&__row {
		flex-grow: 1;
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 16px;
		align-items: center;
		width: 100%;
	}

	label {
		margin-bottom: 0;
	}

	&__delete {
		&-time {
			// margin-left: auto;
			font-size: 17px;
			font-weight: 400;
			line-height: 20px;
			margin-top: auto;
			width: 24px;
			height: 24px;
			margin-bottom: 8px;
		}

		&-foto {
			font-size: 17px;
			font-weight: 400;
			line-height: 20px;
			margin-left: 8px;
			width: 24px;
			height: 24px;
			align-self: center;
		}

		&-dish {
			display: flex;
			align-items: center;
			margin: 0 0 0 38px;

			svg {
				margin-right: 8px;
			}
		}
	}

	&__add-time {
		// margin-left: auto;
		font-size: 17px;
		font-weight: 400;
		line-height: 20px;
		margin-top: auto;
		width: 24px;
		height: 24px;
		margin-bottom: 8px;
		display: flex;
		justify-content: center;
		font-size: 22px;
	}


	&__items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 16px;

		.popup__row {
			width: calc(50% - 8px);
			flex-grow: 0;
		}
	}

	&__add-tel {
		font-size: 17px;
		font-weight: 400;
		line-height: 20px;

		&:before {
			content: "+";
			margin-right: 8px;
			font-size: 22px;
			margin-bottom: 2px;
		}
	}

	&__copy {
		border-radius: 4px;
		border: solid 1px #c8c9cc;
		background: #ffffff;
		padding: 8px 12px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		position: relative;

		a {
			&:before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
			}
		}

		.tooltip {
			position: absolute;
			width: 100% !important;
			height: 100%;
			left: 0;
			top: 0;
			background: white;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}

	select {
		width: auto;
		height: 40px;
		border-radius: 4px;
		border: solid 1px #c8c9cc;
		padding: 0 12px;
		-webkit-appearance: none;
		appearance: none;
		flex-grow: 1;
		background: url("../images/FormBasic-Field.svg") #ffffff no-repeat 98% center;
	}

	&__time {
		border-radius: 8px;
		background: #F4F4F4;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: 16px 8px 16px 16px;
		margin: 0 0 16px;
		width: calc(100% + 36px);

		.popup__field {
			display: flex;
			width: 100%;
			justify-content: space-between;
			margin-bottom: 12px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		.popup__row {
			width: auto;
			flex-grow: 0;
			flex-direction: column;
			align-items: flex-start;

			&:nth-last-of-type(-n+3) {
				margin-bottom: 0;
			}
		}

		.popup__subtitle {
			width: auto;
		}

		select {
			width: 140px;
			height: 40px;
		}

		input {
			width: 90px;
			height: 40px;
			border-radius: 4px;
			border: solid 1px #c8c9cc;
			background: #ffffff;
			flex-grow: 0;
			padding-right: 0;
		}
	}

	&__cancel {
		display: flex;
		align-items: center;
		width: 152px;
		justify-content: center;
		margin-right: auto;
	}

	&__pic {
		height: 280px;
		margin-bottom: 16px;
		max-width: 100%;
		width: 100%;
	}

	&__pic,
	&__logo {
		position: relative;
		border-radius: 8px;
		overflow: hidden;
		border: solid 1px #f4f4f4;

		img {
			max-width: 100%;
			max-height: 100%;
			// position: absolute;
			// width: 100%;
			// height: 100%;
			object-fit: contain;
			background: white;
			// z-index: 1;
		}

		.file {
			width: 100%;
			height: 100%;
			border-radius: 5px;
			display: flex;
			flex-direction: column-reverse;
			transition: border 300ms ease;
			cursor: pointer;
			align-items: center;
			justify-content: center;
			position: absolute;
			padding: 0 21px;
			z-index: 2;
			opacity: 0;
			margin-bottom: 0px;
			top: 0;
			left: 0;

			&__section {
				width: 100%;
			}

			&__button {
				width: auto;
				padding: 0 12px;
				height: auto;
			}

			&__footer {
				padding-bottom: 0;
			}

			&__field {
				margin: 8px 0 0;
			}

			span {
				font-size: 16px;
				font-weight: 500;
				line-height: 21px;
				text-align: center;

				&.add {
					color: #81858b;
				}

				&.edit {
					color: #ffffff;
					z-index: 3;
				}
			}



			&:hover {
				background: rgba(0, 0, 0, 0.29);
				transition: 0.3s;
				opacity: 1;
			}

		}

		.stub {
			// position: absolute;
			// top: 0;
			// left: 0;
			width: 100%;
			height: 100%;
		}
	}

	&__logo {
		height: auto;
		margin-bottom: 0px;
		max-width: 100%;
		width: auto;
		flex-grow: 1;
		min-height: 48px;
		max-height: 144px;
		display: flex;
		justify-content: center;

		img {}


	}

	&__add-file {
		position: relative;
		font-size: 17px;
		font-weight: 400;
		line-height: 20px;
		margin-bottom: 0;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 32px;
		margin-bottom: 16px;



		.file {
			&__button {
				width: auto;
				height: auto;
				font-weight: 400;

				&:before {
					content: "+";
					margin-right: 8px;
					font-size: 22px;
					margin-bottom: 2px;
				}
			}

			&__footer {
				padding-bottom: 0;
			}
		}
	}

	&__qrcode {
		display: flex;
		align-items: center;

		svg {
			margin-right: 8px;
		}

		&-done {
			// margin-top: auto;
		}
	}

	&__download {
		display: flex;
		align-items: center;
		margin-left: auto;
		// margin-top: 16px;

		a {
			display: flex;
			align-items: center;
			margin-left: 12px;
		}
	}

	&__submit {
		height: 40px;
		border-radius: 4px;
		background: var(--blue);
		-webkit-appearance: none;
		color: #fff;
		font-size: 17px;
		font-weight: 400;
		line-height: 20px;
		text-align: center;
		border: 0;
		cursor: pointer;
		padding: 0 30px;
		margin-right: 0;
		// margin-left: auto;
	}

	&__remove {
		padding: 0 16px;
		display: flex;
		align-items: center;
		margin-right: auto;

		svg {
			margin-right: 12px;
		}
	}

	.swiper {
		max-width: 100%;
		position: relative;



		&-slide {
			border-radius: 16px;
			border: solid 1px #f4f4f4;
			overflow: hidden;
		}

		.swiper-pagination {
			width: 100%;
			margin-top: 16px;

			&-bullet {
				width: 7px;
				height: 7px;
				opacity: 0.6;
				background: #007bff;

				&-active {
					opacity: 1;
					transform: unset;
				}
			}
		}


	}

	&__dish {
		&-caption {
			font-size: 20px;
			font-weight: 500;
			line-height: 27px;
			margin-bottom: 12px;
		}

		&-desc {
			font-size: 16px;
			font-weight: 400;
			line-height: 22px;
			margin-bottom: 10px;
		}

		&-subtitle {
			font-size: 16px;
			font-weight: 500;
			line-height: 21px;
			margin-bottom: 12px;
		}

		&-consist {
			color: #868686;
			font-size: 16px;
			font-weight: 400;
			line-height: 22px;
			margin-bottom: 12px;
			width: 100%;
		}

		&-info+&-info {
			margin-left: auto;
			margin-right: 16px;
		}

		&-more {
			font-size: 20px;
			font-weight: 500;
			line-height: 22px;

			&.color {
				color: var(--blue);
			}

			span {
				font-size: 16px;
				font-weight: 500;
				line-height: 21px;
				color: #868686;

				&:before {
					content: "/";
					margin: 0 4px;
				}
			}
		}

		&-link {
			display: flex;
			align-items: center;

			svg {
				margin-right: 8px;
			}
		}

		&-table {
			width: 100%;

			table {
				width: 100%;
			}

			tr {
				margin-bottom: 8px;
			}

			th,
			td {
				padding-right: 8px;
				padding-bottom: 8px;
			}

			th {
				color: #81858b;
				font-size: 14px;
				font-weight: 500;
				line-height: 19px;
			}

			td {
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
			}
		}

		&-text {
			color: #868686;
			font-size: 12px;
			font-weight: 400;
			line-height: 16px;
		}
	}

	&__images {
		max-width: 100%;
		display: flex;
		flex-direction: column;

		&-main {
			position: relative;
			display: flex;
			margin-bottom: 16px;

			.swiper-large__item {
				&:before {
					content: '';
					border-radius: 8px;
					border: solid 1px #f4f4f4;
					background: rgba(0, 0, 0, 0.29);
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
					left: 0;
				}
			}

			&:hover {

				.popup__images-link {
					display: block;
				}
			}
		}

		&-link {
			color: #ffffff;
			position: absolute;
			font-size: 16px;
			font-weight: 500;
			line-height: 21px;
			text-align: center;
			width: 100%;
			top: 50%;
			transform: translateX(-50%)translateY(-50%);
			left: 50%;
			display: none;
		}

		&-choice {
			display: flex;
			margin-bottom: 16px;
			// overflow-x: auto;
		}

		&-item {
			border-radius: 8px;
			border: solid 1px #f4f4f4;
			overflow: hidden;
			position: relative;
			margin-right: 14px;
			width: 75px;
			max-height: 70px;
			flex-shrink: 0;

			&:last-child {
				margin-right: 0;
			}
		}

		&-delete {
			position: absolute;
			top: 0;
			right: 0;
			border-radius: 8px;
			background: #007bff;
			width: 24px;
			height: 24px;
			cursor: pointer;

			&:after,
			&:before {
				position: absolute;
				left: 50%;
				margin: -1px 0 0 -8px;
				top: 50%;
				height: 2px;
				width: 16px;
				background: white;
				content: "";
				transform-origin: 50% 50%;
			}

			&:before {
				transform: rotate(-45deg);
			}

			&:after {
				transform: rotate(45deg);
			}
		}

		&-add {
			font-size: 17px;
			font-weight: 400;
			line-height: 20px;
			text-align: center;
			border-radius: 4px;
			border: solid 1px var(--blue);
			background: #ffffff;
			width: 183px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			align-self: center;

			&:before {
				content: "+";
				margin-right: 8px;
				font-size: 22px;
				margin-bottom: 2px;
			}
		}

		.swiper-large {
			margin-bottom: 24px;
		}


	}

	&__glad {
		font-size: 16px;
		font-weight: 400;
		line-height: 22px;
		text-align: center;
		margin-bottom: 16px;
	}

	&__lang {
		position: relative;
		width: 100%;
		max-height: 130px;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding-bottom: 60px;

		svg {
			position: absolute;
			top: 40px;
			right: 8px;
		}

		.popup__submit {
			width: 140px;
			height: 40px;
			flex-shrink: 0;
			margin-right: 0;
			position: absolute;
			bottom: 0;
			z-index: 6;
		}
	}

	&__list {
		border-radius: 4px;
		border: solid 1px #c8c9cc;
		background: #ffffff;
		list-style: none;
		padding-left: 0;
		height: auto;
		width: 100%;
		display: flex;
		flex-direction: column;


		// max-height: 40px;

		li {
			display: none;
			font-size: 16px;
			font-weight: 400;
			line-height: 20px;
			height: 40px;
			align-items: center;
			padding-left: 12px;

			a {
				color: $black;
			}

			&.active {
				display: flex;
				order: -2;
			}
		}

		&.active {
			z-index: 8;

			li {
				display: flex;
			}
		}


	}

	&__category {
		display: flex;
		flex-direction: column;
		width: 100%;

		.popup {
			&__field {
				width: 100%;
				display: flex;
				position: relative;
			}

			&__row {
				width: auto;
				display: flex;

				&:first-of-type {
					width: 310px;
					margin-right: 32px;
				}
			}

			&__input {}

			&__subtitle {}

			&__delete-category {
				margin-bottom: 20px;
				display: flex;
				margin-top: auto;


				svg {}



				&:hover {}

				&:active {}

				&:focus {}
			}

			&__add-category {
				display: flex;
				align-items: center;
				margin-bottom: 24px;

				&:before {
					content: "+";
					margin-right: 8px;
					font-size: 22px;
					margin-bottom: 2px;
				}

				&:hover {}

				&:active {}

				&:focus {}
			}

			&__cancel {
				margin-right: 21px;
			}
		}

		.sort__button {
			position: absolute;
			width: 20px;
			height: 32px;
			top: 50%;
			transform: translateY(-50%);
			left: 5px;
			display: none;
			cursor: pointer;

			&:before {
				width: 12.5px;
				height: 12px;
				content: '';
				background: url('../images/sorting.svg')rgba(255, 255, 255, 0.85) center no-repeat;
			}
		}

		.form__elem {
			.form__elem-input {}

			.form__elem-label {}
		}

		&.sortable {
			.popup {
				&__field {
					border-radius: 8px;
					background: #ffffff;
					box-shadow: 0px 4px 12px rgba(23, 29, 41, 0.08);
					width: calc(100% + 38px);
					margin: 0 -24px 8px;
					padding: 0 18px 0 30px;
				}
			}

			.sort__button {
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}

	}

	&__col {
		display: flex;
		width: 100%;

		.popup {
			&__row {
				width: auto;

				&:first-of-type {
					flex-grow: 1;
					width: 216px;
					margin-right: 24px;
					flex-shrink: 0;
				}

				.form__elem {
					align-self: flex-start;
				}
			}

			&__subtitle {
				display: flex;
			}
		}
	}

	&__checkbox {
		width: 100%;
		display: flex;
		flex-wrap: wrap;

		.form__elem {
			width: calc(50% - 8px);
			margin-bottom: 15px;
		}
	}

	&__caption {
		font-size: 16px;
		font-weight: 500;
		line-height: 21px;
		width: 100%;
		margin-bottom: 17px;
	}

	&__more {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		justify-content: space-between;

		.popup__row {
			width: calc(100%/3 - 11px);
			flex-grow: 0;
		}
	}

	&__excerpt {
		font-size: 13px;
		font-weight: 400;
		line-height: 22px;
		margin-bottom: 16px;

		p {
			margin-bottom: 0;
		}
	}

	&__boss {
		.popup__submit {
			width: 100%;
		}
	}

	@media (max-width: 767px) {
		&__table {
			display: table;
			width: 100%;
			height: 100%;
			padding: 40px 0;
		}

		&__title {
			font-size: 16px;
			font-weight: 500;
			line-height: 21px;
			text-align: center;
			// margin-bottom: 16px;
			text-align: center;
			max-width: 62%;
			margin: 0 auto 16px;
		}

		&__text {
			font-size: 12px;
			line-height: 1.5;
			color: #000000;
			margin-bottom: 16px;
		}

		&__block {
			width: auto;
			padding: 24px;
		}
	}
}
